        background:url(https://www.bosch-ebike.com/fileadmin/_processed_/8/8/csm_Bosch-eBike-DTF-Elsass-MY2020-24_Print__300_dpi__p2_85567d1910.jpg?_=1592980405);"
        @click="$router.push('/finder')"
      >
