        background:url(data:image/jpeg;base64,);"
        @click="$router.push('/track')"
      >
        <v-card-text class="text-center content-text">
          Bike Tracker
        </v-card-text>
      </v-card>

      <v-card
        v-if="!frontLight"
        @click="switchFrontLight"
        width="40%"
        height="12rem"
