        </v-card-title>
        <v-card-text class="text-center content-text-s">
          ON
        </v-card-text>
      </v-card>

      <v-card
        v-else
        @click="switchFrontLight"
        width="40%"
        height="12rem"
        class="pa-2"
        color="{frontLight:red}"
        rounded="xl"
        style="margin:0.5rem;padding:0.5rem;"
      >
        <v-card-title class="headline title-text">
