</template>

<script>
import XDBike from "@/components/3D-Bike.vue";

export default {
  name: "DashBoard",
  components: { XDBike },
  data() {
    return {
      timer: null,
    };
  },
  computed: {
    temperature() {
      return this.$store.state.environment.temperature;
    },
    altitude() {
      return this.$store.state.environment.altitude;
    },
    speed() {
      return this.$store.state.environment.speed;
    },
    pressure() {
      return this.$store.state.environment.pressure;
    },
    frontLight: {
      get() {
        return this.$store.state.device.light.light1;
      },
      set(val) {
        this.$store.state.device.light.light1 = val;
      },
    },
    backLight: {
      get() {
        return this.$store.state.device.light.light2;
      },
      set(val) {
        this.$store.state.device.light.light2 = val;
      },
    },
  },
  methods: {
    async switchFrontLight() {
      this.frontLight = !this.frontLight;
      await this.$store.dispatch("updateLight1", this.frontLight);
    },
    async switchBackLight() {
      this.backLight = !this.backLight;
      await this.$store.dispatch("updateLight2", this.backLight);
    },
    async update() {
      await this.$store.dispatch("updatePressure");
      await this.$store.dispatch("updateTemperature");
      await this.$store.dispatch("updateGeo");
    },
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  mounted() {
    this.timer = setInterval(this.update, 1000);
  },
};
</script>
